<template>
  <div class="calling-banner">
    <div class="image-wrapper">
      <i class="fas fa-phone"></i>
    </div>
    <div class="calling-text">
      calling
      <br>
      {{dots}}
    </div>
  </div>
</template>
<script>
import { setInterval } from "timers";
export default {
  data() {
    return {
      dots: ""
    };
  },
  mounted() {
    let count = 0;
    setInterval(() => {
      count++;
      count = count % 5;
      let temp = ".";
      for (let i = 0; i < count; i++) {
        temp += ".";
      }
      this.dots = temp;
    }, 500);
  }
};
</script>

<style>
.calling-banner {
  position: fixed;
  z-index: 3;
  top: 0;
  display: grid;
  align-items: end;
  text-align: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(221, 192, 192, 0.932);
  color: rgb(51, 39, 39);
}

.calling-banner .image-wrapper i {
  display: inline-block;
  margin: 0 auto;
  width: 62px;
  font-size: 180px;
  animation: calling-banner-anim 1000ms infinite alternate;
  animation-delay: 1000ms;
}
.calling-banner .calling-text {
  font-size: 75px;
  margin-bottom: 15vh;
}
@keyframes calling-banner-anim {
  0% {
    transform: translateX(-25px) rotate(-15deg);
  }
  20% {
    transform: rotate(15deg) translateX(-35px);
  }
  30% {
    transform: translateX(-25px) rotate(-15deg);
  }
  70% {
    transform: rotate(15deg) translateX(-35px);
  }
}
</style>



