<template>
  <div>
    <incomming-call-banner @decline="decline" @accept="accept" v-if="showIncommingCallBanner"></incomming-call-banner>
    <calling-banner v-if="showCallingBanner"></calling-banner>
    <div :class=" onCall ? 'top-wrapper on-call' : 'top-wrapper'">
      <div class="id-text">
        <span>ID :</span>
        {{peerId}}
      </div>
    </div>
    <div v-if="!onCall" class="call-wrapper">
      <div class="header">
        <span class="one">PEER</span>
        <span class="two">TALK</span>
      </div>
      <div class="banner">Konuşmanın peer'i</div>
      <div class="form-wrapper">
        <input placeholder="Peer id giriniz" type="text" v-model="remoteCallId">
        <button @keydown.enter="peerCall" @click="peerCall">CALL</button>
      </div>
    </div>

    <div v-if="onCall" class="video-wrapper">
      <div class="remote-video">
        <video
          style="background-color:gray;width:auto; height: 100vh;background-color:black;"
          autoplay
          playinline
          id="remoteStreamVideo"
        ></video>
      </div>
      <div class="self-video">
        <video
          style="background-color:gray;width:100%; height: auto;"
          muted
          autoplay
          playinline
          id="localStreamVideo"
        ></video>
      </div>
      <div class="call-menu-bar">
        <button @click="endCall" class="end-call">
          <i class="fa fa-phone"></i>
        </button>
      </div>
    </div>
  </div>
</template>


<script>
import CallingBanner from "../components/CallingBanner";
import IncommingCallBanner from "../components/IncommingCallBanner";

const callConstraint = { video: true, audio: true };

export default {
  data() {
    return {
      onCall: false,
      showCallingBanner: false,
      isCalling: false,
      showIncommingCallBanner: false,
      callEventEmitter: new EventTarget(),
      remoteCallId: "",
      peerId: "",
      peer: null,
      call: null
    };
  },
  mounted() {
    this.initiate();
  },
  components: {
    CallingBanner,
    IncommingCallBanner
  },
  methods: {
    accept() {
      this.callEventEmitter.dispatchEvent(new Event("accept"));
      this.showIncommingCallBanner = false;
    },
    decline() {
      this.showIncommingCallBanner = false;
    },
    peerCall() {
      this.showCallingBanner = true;
      let id = this.remoteCallId;
      //Get data from user media
      navigator.getUserMedia(
        callConstraint,
        stream => {
          //Establishing connection
          let call = this.peer.call(this.remoteCallId, stream);
          call.on("stream", remoteStream => {
            this.showCallingBanner = false;
            this.onCall = true;
            // Show stream  in video element
            let videoElement = document.getElementById("remoteStreamVideo");
            let localVideoElement = document.getElementById("localStreamVideo");
            localVideoElement.srcObject = stream;
            videoElement.srcObject = remoteStream;
          });
        },
        err => {
          console.log("Failed to get local stream", err);
        }
      );
    },
    endCall() {
      this.onCall = false;
      this.showCallingBanner = false;
      this.isCalling = false;
      this.showIncommingCallBanner = false;
      this.initiate();
    },
    initiate() {
      //Generate random user id
      this.peerId = `trd-${Math.floor(Math.random() * 10000)}`;
      //Instantiate peer
      this.peer = new Peer(this.peerId);
      //listening for call
      this.peer.on("call", call => {
        if (this.onCall) return;
        navigator.getUserMedia(
          callConstraint,
          stream => {
            this.showIncommingCallBanner = true;
            this.callEventEmitter.addEventListener("accept", e => {
              call.answer(stream);
              call.on("stream", remoteStream => {
                this.showCallingBanner = false;
                this.onCall = true;
                // Show stream  in video element
                let videoElement = document.getElementById("remoteStreamVideo");
                videoElement.srcObject = remoteStream;
                let localVideoElement = document.getElementById(
                  "localStreamVideo"
                );
                localVideoElement.srcObject = stream;
              });
              this.callEventEmitter = new EventTarget();
            });
          },
          err => {
            console.log("Error occured", err);
          }
        );
      });
    }
  }
};
</script>


<style>
.top-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.4rem;
  background-color: #2f6073;
  border-bottom-right-radius: 15px;
  box-shadow: 2px 2px 2px black;
  opacity: 0.85;
  transition: 200ms;
}
.top-wrapper.on-call {
  background-color: transparent !important;
  box-shadow: none;
}
.top-wrapper:hover {
  opacity: 1;
}
.top-wrapper .id-text {
  color: white;
  font-size: 150%;
}
.top-wrapper .id-text span {
  color: rgb(20, 120, 138);
}

.call-wrapper {
  text-align: center;
  margin-top: 20vh;
}

.call-wrapper .header {
  font-size: 98px;
  perspective: 1000px;
  letter-spacing: 7px;
}

.call-wrapper .header .one {
  display: inline-block;
  transform: translateX(25px) rotateY(45deg) rotateX(25deg);
}
.call-wrapper .header .two {
  display: inline-block;
  transform: translateX(-25px) rotateY(-45deg) rotateX(35deg);
}
.call-wrapper .banner {
  margin-bottom: 1.5rem;
  margin-top: 0.5rem;
  letter-spacing: 10px;
  opacity: 0.7;
}
.call-wrapper .form-wrapper * {
  box-shadow: 0px 5px 1px 1px rgb(30, 28, 31);
  padding: 0.6rem 0.8rem;
  font-size: 110%;
  outline: none;
  border: none;
}
.call-wrapper .form-wrapper input {
  color: #011526;
  background-color: #f2f2f2;
}

.video-wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  display: grid;
  align-items: center;
  background-color: black;
  overflow: hidden;
}

.video-wrapper .remote-video {
  text-align: center;
}
.video-wrapper .self-video {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 300px;
  box-shadow: -5px -5px 5px -4px black;
}

.video-wrapper .call-menu-bar {
  position: absolute;
  bottom: 30px;
  width: 100%;
  text-align: center;
}
.video-wrapper .call-menu-bar button {
  border: none;
  outline: none;
  opacity: 0.9;
  background-color: rgb(82, 19, 19);
  color: #f2f2f2;
  border-radius: 50%;
  font-size: 150%;
  padding: 1.2rem;
  transition: 300ms;
  cursor: pointer;
  transform: rotate(225deg);
}

.video-wrapper .call-menu-bar button:hover {
  opacity: 1;
}
@media only screen and (max-width: 480px) {
  .video-wrapper .self-video {
    width: 200px;
    top: 0;
    box-shadow: none;
  }
}
</style>



